body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #7A313B;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app-container {
  width: 100%;
  max-width: 400px;
}

.todo-container {
  background: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 30px;
}

.todo-title {
  text-align: center;
  color: #333;
  font-size: 24px;
  margin-bottom: 20px;
}

.input-section {
  display: flex;
  gap: 10px;
}

.task-input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #b3b1b1;
  border-radius: 5px;
  font-size: 16px;
}

.add-btn {
  background-color: #A53C4B;
  color: #fff;
  border: none;
  border-radius: 15px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.add-btn:hover {
  background-color: #7A323C;
}

.task-list {
  list-style: none;
  padding: 0;
  margin-top: 20px;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fdfcfc;
  padding: 10px;
  border: 1px solid #7A323C;
  border-radius: 5px;
  margin-bottom: 10px;
  font-size: 16px;
}

.action-buttons {
  display: flex;
  gap: 10px;
}

.edit-btn {
  background-color: #545351;
  border: none;
  color: #fff;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.edit-btn:hover {
  background-color: #7A323C;
}

.delete-btn {
  background-color: #545351;
  border: none;
  color: #fff;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.delete-btn:hover {
  background-color: #7A323C;
}

@media (max-width: 768px) {
  .todo-container {
    padding: 15px;
  }

  .task-input {
    font-size: 14px;
  }

  .add-btn {
    font-size: 14px;
    padding: 8px 15px;
  }

  .edit-btn,
  .delete-btn {
    font-size: 12px;
    padding: 5px 8px;
  }
}
